$(document).ready(function() {
    $('input[type="tel"]').mask('+7 999 999-9999', { autoclear: false }, { placeholder: '+7             ' });

    $(".m-select").niceSelect()

    new AirDatepicker('.date-input', {
        locale: localeRu,
        // inline: true,
        // isMobile: true,
        autoClose: true,
        onSelect: () => {
            $(".date-input").addClass("input-active")
        }
    })

    $(".rating-input__item").click(function() {
        let ratingItem = parseInt($(this).attr("data-rating-value"))
        $(this).parent(".rating-input").attr("data-total-rating", ratingItem)
        $(this).siblings(".rating-input__field").val(ratingItem)
    })

    Fancybox.defaults.placeFocusBack = false
});










