$(".stat-card__progress-fill").each((i,el) => {
    let maxRating = +$(el).attr("data-max")
    let currentRating = +$(el).attr("data-current")

    $(el).css("width",`${currentRating/maxRating * 100}%`)
})


$(".like").click(function () {
    let likes = $(this).find(".like__count")
    let likesCount = +$(likes).html()

    if($(this).hasClass("_active")){
        $(this).removeClass("_active")
        $(likes).html(likesCount-1)
    } else{
        $(this).addClass("_active")
        $(likes).html(likesCount+1)
    }
})
