function fileSlider() {
    var swiper = new Swiper('.file .swiper-container', {
        slidesPerView: 'auto',
    })

    function addSlide(img) {
        let slide = `
            <div class="swiper-slide img-file">
                <img src="${img}" alt="">
                <button type="button" class="img-file__remove m-btn">
                    <img src="img/remove.svg" alt="">
                </button>
            </div>
        `
        swiper.appendSlide(slide);

        $(".img-file__remove").click(function () {
            let countOfSlides = $(".file .swiper-slide").length
            if(countOfSlides === 1){
                $(this).parents(".file").removeClass("_has-file")
            }
            var i = $(this).parents('.swiper-slide').index();
            swiper.removeSlide(i);
        })
    }



    $(".img-upload").change(function (event) {

        if(event.target.files && event.target.files[0]) {
            let reader = new FileReader();
            reader.onload = function (e) {
                addSlide(e.target.result)
            };
            reader.readAsDataURL(event.target.files[0]);
        }

        $(this).parents(".file").addClass("_has-file")
    })
}

fileSlider()
